<template>
  <div class="warp">
    <Header :text="title" v-show="!hideNavi"></Header>
    <div class="container">
      <div id='v-html-only' class="vhtml" v-html="message" v-remove-table-value>
      </div>
    </div>
  </div>
</template>
<script>
import URL from '@/http/url'
import Header from '@/components/header'
import utils from '@/utils/utils'
export default {
  components: {
    Header
  },
  directives: {
    'remove-table-value': {
      // 优化table列表超过宽度
      update: function(el, binding, vnode, oldVnode) {
        el.querySelectorAll('table').forEach(item => {
          item.setAttribute('width', 'none')
          item.setAttribute('style', 'width:100%;max-width:100%');
          item.querySelectorAll('td').forEach(td => {
            td.setAttribute('style', 'width:none');
          })
        })
      }
    }
  },
  data() {
    return {
      message: '',
      title: undefined,
      hideNavi: utils.shouldHideNaviBar(),
      // isHaierApp: false,
    }
  },
  created() {
    // this.isHaierApp = utils.isHaierApp();
    if (this.$route.query.id) {
      this.title = this.$route.query.title
      this.getInfo();
    } else {
      // this.$toast('无效参数')
    }
  },
  mounted() {
    // 解决文档中 配置超链接导致的 在智家APP中关闭容器异常的问题
    console.log('00000000000')
    document.querySelector('#v-html-only').addEventListener('click', (e) => {
      console.log(e)
      e.preventDefault()
      let dom = e.target
      while (!(dom.tagName.toUpperCase() === 'A' && dom.getAttribute('href')) && dom.parentNode) {
        dom = dom.parentNode
      }
      if (dom.tagName.toUpperCase() === 'A' && dom.getAttribute('href')) {
        const href = dom.getAttribute('href')
        console.log('bbbbbbbbb')
        let flag = href.indexOf('mobile.testehaier.com') !== -1 || href.indexOf('m.ehaier.com') !== -1
        this.$util.openNewWindow(href, true, false, !flag)
      }
    }, false)
  },
  watch: {
    '$route': function(newValue, oldValue) {
      if (this.$route.path === '/documentInfo' && this.$route.query.id) {
        this.title = this.$route.query.title
        this.getInfo();
      } else {
        // this.$toast('无效参数')
      }
    }
  },
  methods: {
    getInfo() {
      this.$http.get(URL.GET_HELP_RICHTEXT + `?id=${this.$route.query.id}`).then(res => {
        if (res.data.success && res.data.data) {
          this.title = res.data.data.title
          this.message = res.data.data.context.replace(/src="http:\/\//g, 'src="https://') // 把富文本中的图片链接全部改为https开头的
          this.$util.gioTrackWithVariable('help_articleDetail_view', {
            article_id: (res.data.data.id + '') || null,
            article_Detail: this.title || null,
          })
        } else {
          this.$toast(res.data.message)
        }
      }).catch(e => {
        this.$toast('网络不佳，请检查网络后重试!')
      })
    }
  }
}
</script>
